function HanoiView(game, element) {
  this.game = game;
  this.element = element;
  this.setupTowers();
  this.render();
  this.clickedTower = null;
}

HanoiView.prototype.setupTowers = function () {
  this.game.towers.forEach( (tower, i) => {
    const $tower = $("<ul>").addClass("tower")
          .attr("data-pos", i).text(`Tower ${i}`);
    tower.forEach(disk => {
      let $disk = $("<li>").addClass("disk")
          .attr("data-size", disk).text(`Disk ${disk}`);
      $tower.append($disk);
    });
    this.element.append($tower);
    $tower.on("click", this.clickTower);
  });

};

HanoiView.prototype.render = function () {
};

HanoiView.prototype.clickTower = function(event) {
  const $tower = $(event.currentTarget);
  // console.log($tower);
  let pos = $tower.data("pos");
  // console.log(typeof $tower);
  // console.log($tower);
  console.log(typeof this.clickedTower);
  // console.log(this.clickedTower);

  if (typeof this.clickedTower === "object") {
    console.log(this);
    console.log(this.clickedTower);
    this.clickedTower.removeClass("clicked");
    this.clickedTower = null;
    this.game.move(this.clickedTower.data("pos"), pos);
  } else {
    // console.log($tower);
    this.clickedTower = $tower;
    // console.log(this.clickedTower);
    $tower.addClass("clicked");
  }
};

